import gql from "graphql-tag";
import { useMutation } from "@apollo/react-hooks";

import { UserContext } from "src/userContext";

export const LOGINEXTERNAL = gql`
mutation loginExternal(
        $email: String!,
        $id: String!
      ){
        loginExternal(email: $email, id: $id) {
            dataOut{
                success
                name
                _id
                token
                password
                email
                }
            errorOut{
                  name
                  message
                }
        }
      }
`;

